import React, { Component } from 'react';
import '../styles/Header.css';
import logo from '../images/logo.png';
import logo2 from '../images/logo2.png';

// https://react.semantic-ui.com/


class Header extends Component {

  render() {
    return (
      <header className="header">
        <div className="" id="">
          <div className="row ">
            <div className="options" >
              <img src={logo} className="logoBanner" alt="logo" />
              <h2>Veja os pets disponiveis </h2>
              <p>Utilize agora nossa plataforma para doar ou adotar um pet. Utilize agora nossa plataforma para doar ou adotar um pet.
                Utilize agora nossa plataforma para doar ou adotar um pet.</p>

              <br></br>
              <div className="botoes">
                <a id="contact-button" class="botoes-primary"
                  download >Adotar um animal</a>
                  <br></br>
                <a id="contact-button" class=" botoes-sencondary"
                  download >Doar um animal</a>
              </div>
            </div>

            <div className="options" >
              <img src={logo2} className="logoBanner" alt="logo" />
            </div>
          </div>
        </div>
      </header>
    );
  }
}

export default Header;
